export default class Node {
  constructor ({ ...args }) {
    this.uuid = args.uuid
    this.nextUuid = args.nextUuid
    this.parentUuid = args.parentUuid
    this.prevUuid = args.prevUuid
    this.children = args.children || []
    this.lock = false

    this.title = args.title // 
    this.code = args.code || '' // 
    this.additional = args.additional || false //  falsetrue
    this.automateCondition = args.automateCondition || null //  String
    this.automateConditionNote = args.automateConditionNote || null // string 
    this.componentId = args.componentId || null // ID
    this.computes = args.computes || null //  String
    this.customVar = args.customVar || null //  String
    this.expanded = args.expanded === null || args.expanded === undefined ? true : args.expanded // falsetrue
    this.id = args.id || null // ID
    this.dr = args.dr || null // 
    this.jumpCondition = args.jumpCondition || null //  String
    this.macroLogic = args.macroLogic || null //  String
    this.mustFlag = (args.mustFlag === null || args.mustFlag === undefined) ? true : args.mustFlag //  falsetrue
    this.unique = !!args.unique //  falsetrue
    // this.unique = args.unique //  falsetrue
    this.point = args.point || '' // 
    this.nestProjectId = args.nestProjectId || null // ID
    this.nestProjectTitle = args.nestProjectTitle || false // 
    this.parentId = args.parentId // IDs
    this.permissionCondition = args.v || null //  String
    this.premissionConditionNote = args.premissionConditionNote || null // string 
    this.projectId = args.projectId || null // ID
    this.remarks = args.remarks || '' //  String
    this.repeatFlag = args.repeatFlag || false // falsetrue
    this.showCondition = args.showCondition || null //  String
    this.sort = args.sort || 1024 // 
    this.tip = args.tip || null //  String
    this.type = args.type || 1 // 12
    this.options = args.options || []
    this.tagInfo = args.tagInfo || []
    this.crossState = args.crossState || false // 
    this.crossProjectEnName = args.crossProjectEnName || '' // 

    this.nodeComponentConfigDatas = args.nodeComponentConfigDatas || [] // 

    this.rejectionCondition = args.rejectionCondition || null // 
    this.nodeConfigData = args.nodeConfigData || null // 

    // this.componentInfo = args.componentInfo || { // 
    //   items: [ // 
    //     {
    //       componentId: null
    //     }
    //   ]
    // }
    this.componentInfo = args.componentInfo || null

    this.jumpConditionNote = args.jumpConditionNote || null //  string
    this.showConditionNote = args.showConditionNote || null // string // 
    this.translationCondition = args.translationCondition || null
    this.translationConditionNote = args.translationConditionNote || null
    this.permissionConditionNote = args.permissionConditionNote || null
    this.validateCondition = args.validateCondition || null
    this.validateConditionNote = args.validateConditionNote || null
  }
}
